import React, { useState, useContext } from 'react';
import Image from "next/image";
import Link from "next/link";

import Style from './NavBar.module.css';
import { ChatAppContext } from '@/Context/ChatAppContext';
import images from "../../assets"; 
import {Model,Error} from "../index";

const NavBar = () => {
  const menuItems = [
    { menu: "All Users", link: "/alluser" },
    { menu: "Chat", link: "/" },
    { menu: "Contact", link: "/" },
    { menu: "Settings", link: "/" },
  ];

  const [active, setActive] = useState(2);
  const [openModel, setOpenModel] = useState(false);
  const [open, setOpen] = useState(false); // âœ… added missing state

  const { account, userName, connectWallet,createAccount,error } = useContext(ChatAppContext);

  return (
    <div className={Style.NavBar}>
      <div className={Style.NavBar_box}>
        {/* Left: Logo */}
        <div className={Style.NavBar_box_left}>
          <Image src={images.logo} alt="Logo" width={50} height={50} />
        </div>

        {/* Right: Menu + Buttons */}
        <div className={Style.NavBar_box_right}>
          {/* Menu */}
          <div className={Style.NavBar_box_right_menu}>
            {menuItems.map((el, i) => (
              <div
                key={i}
                onClick={() => setActive(i + 1)}
                className={`
                  ${Style.NavBar_box_right_menu_items} 
                  ${active === i + 1 ? Style.active_btn : ""}
                `}
              >
                <Link href={el.link} className={Style.NavBar_box_right_menu_items_link}>
                  {el.menu}
                </Link>
              </div>
            ))}
          </div>

          {/* Connect Wallet */}
          <div className={Style.NavBar_box_right_connect}>
            {account === "" ? (
              <button onClick={connectWallet}>
                <span>Connect Wallet</span>
              </button>
            ) : (
              <button onClick={() => setOpenModel(true)}>
                <Image
                  src={images.user}
                  alt="User"
                  width={25}
                  height={25}
                />
                <small>{userName || "Create Account"}</small>
              </button>
            )}
          </div>
        </div>
      </div>
      {openModel &&(
        <div className={Style.modelBox}>
          <Model openModel={setOpenModel}
          title="WLECOME TO"
          head="CHAT BUDDY"
          info ="BHUAN"
          smallInfo="select your anme"
          images={images.logo}
          functionName={createAccount}
          />


        </div>
      )}
    </div>
  );
};

export default NavBar;
